.container {
    height: 100%;
    padding: 10px 40px 10px 40px;
    /* grid-template-columns: repeat(5, 1fr); */
    display: grid;
    grid-gap: 3px;
    grid-template-columns: repeat(5, 20%);
    grid-template-rows: 80px 500px 550px 330px 500px 300px 800px 70px 500px;
    grid-template-areas:
        "h h h m m"
        "c c c c c"
        "f f f f f"
        "t t t t t"
        "p p p p p"
        "d r r r r"
        "u u u u u"
        "o o o o o"
        "i i i i i"
    ;
}

.header {
    /* grid-column-start: 1;
    grid-column-end: 10; */
    grid-area: h;
    justify-content: left;
    padding-top: 30px;
    position: sticky;
}

.media {
    /* grid-column: 10/13; */
    grid-area: m;
    justify-content: right;
}

.content {
    /* grid-column: 1/-1; */
    grid-area: c;
    justify-content: center;
    /* padding-bottom: -10%; */
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 100%;
}

.iframContent iframe {
    position: absolute;
    top: 0px;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 95%;
    /* background-color: rgb(24, 91, 91); */
}

