    color: rgb(255, 255, 255);
    font-family: DM-Serif-Display;
}

textarea {
    background-color: transparent;
    width: 450px;
    height: 60px;
    border: 1px solid white;
    margin-bottom: 20px;
    justify-content: center;
    margin-right: 40px;
    font-family: DM-Serif-Display;
    font-size: medium;
}

textarea::placeholder {
    color: white;
    font-family: DM-Serif-Display;
}

li {
    list-style-type: none;
}

nav {
    display: flex;
    align-items: center;
    padding-bottom: 20px;
    position: sticky;
}

nav > a {
    text-decoration: none;
    font-family: DM-Serif-Display;
    padding-right: 40px;
    color: black;
    font-size: small;
    font-weight: bolder;
    font-size: 15.5px;
}

nav > a:hover {
    color: #ffffff;
}

